*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
.containerfull{
    width: 100%;
    height: 23px;
    max-height: fit-content;
    background-color: rgb(20, 189, 20);
}
.container{
    width: 90%;
    /* display: flex;
    justify-content: space-between; */
    margin: auto;
    /* align-items: center; */
}
.container h1{
    font-size: 14px;
    color: rgb(245, 241, 245);
    padding: 5px 0;
    display: inline;
}
#navul{
    float: right;
    position: relative;

}
#navul li{
    display: inline;
    margin-right: 10px;
    font-size: 10px;
}
.navbtn{
    float: right;
    display: none;
    width: 16px;
    height: 16px;
    
    border: none;
    background-color: rgb(90, 88, 90);
    color: whitesmoke;
    padding: 3px;
    margin-top: 2.5px;
}

.container .navli{
    list-style: none;
    padding: 0;
    text-decoration: none;
    font-size: 14px;
    color: whitesmoke;
    display: inline-flex;
    margin-right: 12px;
    padding: 3px;
}
.container .navli:hover{
    background-color: white;
    color: rgb(57, 202, 28);
    padding: 3px;
transition: 0.6s;
}
@media (max-width:550px) {
    .navbtn{
        display: block;
    }
    #navul{
        position: absolute;
        width: 90%;
        margin: auto;
        top: 18px;
        background-color: rgb(109, 103, 103);
        z-index: 99;
        transition: 2s;  
        display: none;      
    }
    #navul li{
        text-align: center;
        display: block;
        text-align: center;
        
    }
    .navli{
        border-bottom: 0.3px solid white;
        width: 100%;
        text-align: center;
        
    }
}

/* marqee */
.marqemain{
    width: 100%;
    height: 20px;
    
    background-color: rgb(241, 241, 241);
    color: rgb(13, 206, 100);
}

/* main banner section */
.mainbanner{
    width: 100%;
    /* background-color: greenyellow; */
    background-image: url('./images/backgrdmobils.jpg');
    background-size: cover;
    
}
.subbanner{
    width: 90%;
    height: 80vh;
    margin: auto;
    display: flex;
    justify-content: space-evenly;
    align-items: center;

}

.bannerlist{
    width: 40%;
    /* background-color: rgb(204, 241, 67); */
    padding: 5px 2px 3px 2px;
   
}
.bannerlist h4{
    color: whitesmoke;
}
.bannerlist p{
    font-size: 10px;
    color: rgb(128, 20, 74);
    padding: 5px 0;
}

.bannerlist ul li{
    color: rgb(83, 13, 173);
    padding: 3px;
    font-size: 10px;
    list-style: none;
    margin-top: 7px;
    border-bottom: 1px solid gray;
    transition: 0.5s;
    
}
.bannerlist ul li:hover{
    background-color: rgb(175, 25, 175);
    color: whitesmoke;
}
.bannerimg{
    display: flex;
    justify-content: center;
    width: 40%;
    /* background-image: url('./images/manrepair.jpg');
    background-size: cover; */
    /* animation-name: iimg;
    animation-iteration-count: infinite;
    animation-duration: 2s; */
}
/* @keyframes iimg{
    from {
        width: 230px;
        height: 230px;
    }
    to {
        transition: 2s;
        width: 270px;
        height: 270px;
    }
} */
.bannerimg img{
    width: 200px;
    height: 200px;
    transition: 0.5s;
    border-radius: 50px;
    
}
.bannerimg img:hover{
    width: 210px;
    height: 210px;
    transition: 0.5s;
}
@media (max-width:600px) {
    .subbanner{
        width: 98%;
        flex-direction: column;
        height: fit-content;
    }
    .bannerlist{
        width: 90%;
        margin: auto;
    }
    .bannerimg{
        width: 90%;

       
    }
    .bannerimg img:hover{
        width: 200px;
        height: 200px;
    }
}


/* mobile route */
.mobgrid{
    width: 90%;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-auto-rows: auto;
    grid-column-gap: 10px;
    grid-row-gap: 10px;
}


.mobcard{
    border: 1px solid violet;
    border-radius: 5px;
    padding: 3px;
    background-color: rgb(241, 237, 237);
    
}
.mobcard img{
    height: 100px;
    width: 100%;
    background-size: cover;
}
.mobcard h3{
    font-size: 14px;
    color: rgb(122, 194, 14);
}
.mobcard h6{
    font-size: 12px;
    color: rgb(163, 9, 163);
}
.mobcard p{
    font-size: 9px;
    color: gray;
}
.ratingdiv p{
  display: inline;
}

/* mobiles nested div */
.nestdiv{
    width: 90%;
    
    margin: auto;    
    text-align: center;
}
.nestlist{
    background-color: rgb(23, 235, 23);
    color:aliceblue;
    text-decoration: none;
    padding: 3px;
    font-size: 10px;
    /* margin-left: 35px; */
    border-radius: 3px;
    margin: 5px 35px;   
}
/* service div */

.servisediv{
    background-image: url('./images/transformerimg.jpg');
    height: 90vh;
    width: 100%;
}
@media (max-width:670px) {
    .mobgrid{
        grid-template-columns: repeat(2,1fr);
    }
}
@media (max-width:400px) {
    .mobgrid{
        grid-template-columns: repeat(1,1fr);
    }
    .nestlist{
        margin: 2px 7px;
    }
}
/* strength div */
.strengthdiv{
    background-image: url('./images/backgrdmobils.jpg');
    height: 100vh;
    width: 100%;
    background-size: cover;
}

/* body  */

.containerbody{
    width: 100%;
    
}
.containerbody90{
    width: 90%;
    margin: auto;
    display: flex;
    justify-content:space-between;
    text-align: center;
}

.bodycard{
    width: 150px;
    flex:1;
    border: 0.2px solid violet;
